.label-points {
  display: inline-block;
  padding: 2px 10px;
  border-radius: 3px;
  background-color: #4d4d4d; }
  .label-points .oval {
    width: 16px;
    height: 16px;
    border: 1px solid #333333;
    background-color: #d8d8d8;
    display: inline-block;
    border-radius: 50%;
    vertical-align: middle; }
  .label-points .oval:not(:last-child) {
    margin-right: 2px; }
  .label-points.completed .oval:nth-child(1) {
    background-color: #5fc958; }
  .label-points.on-track .oval:nth-child(2) {
    background-color: #ffbb14; }
  .label-points.cancelled .oval:nth-child(3) {
    background-color: #f45d48; }
