.label-status {
  padding: 6px 7px;
  font-family: "Roboto";
  font-size: 12px;
  font-weight: 500;
  border-radius: 3px;
  display: inline-block;
  min-width: 70px;
  text-align: center; }
  .label-status.completed {
    color: #5fc958;
    background-color: rgba(95, 201, 88, 0.05);
    border: solid 1px #5fc958; }
  .label-status.on-track {
    color: #ffbb14;
    background-color: rgba(255, 187, 20, 0.05);
    border: solid 1px #ffbb14; }
  .label-status.cancelled {
    color: #f45d48;
    background-color: rgba(244, 93, 72, 0.05);
    border: solid 1px #f45d48; }
  .label-status.transparent {
    color: #ffffff;
    border: 1px solid #ffffff;
    background-color: rgba(77, 77, 77, 0.25); }
