.comment-block .comment-card:first-child {
  border-top: 1px solid #d8d8d8; }

.comment-card .card {
  border-bottom: 1px solid #d8d8d8;
  color: #4d4d4d;
  flex-direction: row;
  padding: 1.25rem 0; }
  .comment-card .card .card-body {
    padding: 0 1.25rem; }
  .comment-card .card .user-image {
    background: url("/static/assets/user-image.jpg") center center no-repeat;
    background-size: contain;
    min-width: 37px;
    height: 37px; }
  .comment-card .card .user-name {
    font-size: 13px;
    font-weight: 500;
    color: #4d4d4d;
    font-family: "Roboto-Medium";
    display: block; }
  .comment-card .card .comment-date {
    font-size: 12px;
    color: #4d4d4d;
    display: block;
    margin-bottom: 10px; }
  .comment-card .card .link {
    color: #4d4d4d;
    text-decoration: underline; }
    .comment-card .card .link:hover {
      color: #00adef; }
